.home-page .body-container {
  padding-right: 40px;
}
.home-page .title {
  font-family: 'DINNext-med';
  font-size: 28px;
  font-weight: 500;
  text-align: right;
  color: #32b668;
}
.home-page .sub-title {
  font-family: 'DINNext-reg';
  font-size: 48px;
  text-align: right;
  color: #39404c;
}
.home-page .description {
  font-family: 'DINNext-reg';
  font-size: 24px;
  text-align: right;
  color: #39404c;
  margin-bottom: 50px;
}
.home-page button {
  width: 356px !important;
  height: 68px;
  border-radius: 45px !important;
  color: #ffffff !important;
  font-family: 'DINNext-med';
  font-size: 24px;
  font-weight: 500;
}
.home-page button img {
  padding-right: 20px;
}
.home-page .trainer,
.home-page .trainer:hover,
.home-page .trainer:focus {
  background: #84c774;
  border-color: #84c774;
}
.home-page .training,
.home-page .training:hover,
.home-page .training:focus {
  background: #82cec4;
  border-color: #82cec4;
}
@media (max-width: 767px) {
  .home-page {
    margin-bottom: 20px;
  }
  .home-page .title,
  .home-page .sub-title,
  .home-page .description {
    text-align: center !important;
  }
  .home-page .sub-title {
    font-size: 28px;
  }
  .home-page .description {
    font-size: 16px;
  }
  .home-page button {
    width: 280px !important;
    height: 54px;
    margin-bottom: 20px;
    font-size: 20px;
  }
}
.form-wrapper {
  width: 100%;
  height: auto;
  padding: 40px 60px !important;
  border-radius: 16px;
  border: solid 0.4px #84c7749c;
  background-color: #ffffff;
  margin-bottom: 100px !important;
}
.form-wrapper .title {
  font-family: 'DINNext-reg';
  font-size: 22px;
  text-align: right;
  color: #84c774;
}
.form-wrapper .label {
  font-family: 'DINNext-reg';
  font-size: 18px;
  text-align: right;
  color: #39404c;
}
.form-wrapper .ant-radio-wrapper {
  display: block;
  margin-bottom: 9px;
}
.form-wrapper .ant-radio-wrapper span {
  font-family: 'DINNext-reg';
  font-size: 14px;
  text-align: right;
  color: #1e2944;
}
.form-wrapper .next-btn {
  border-radius: 115px;
  background: #84c774;
  border-color: #84c774;
  width: 174px;
  height: 44px;
}
.form-wrapper .prev-btn {
  font-family: 'DINNext-med';
  font-size: 16px;
  font-weight: 500;
  text-align: right;
  color: #84c774;
  margin-bottom: 0;
  cursor: pointer;
}
.last-step {
  max-width: 482px;
  height: auto;
  width: 100%;
  padding: 61px;
  border-radius: 16px;
  border: solid 0.4px #84c774;
  background: #fff;
  text-align: center;
}
.last-step button,
.last-step button:hover,
.last-step button:focus {
  width: 174px;
  height: 44px;
  background: #84c774;
  border-radius: 20px;
  color: #fff;
}
.last-step .title {
  font-family: 'DINNext-reg';
  font-size: 22px;
  text-align: center;
  color: #84c774;
  margin-bottom: 20px;
}
.last-step img {
  margin-bottom: 50px;
}
@media (max-width: 768px) {
  .form-wrapper {
    border-radius: 0;
    padding: 40px 30px !important;
  }
  .last-step {
    border-radius: 0;
  }
}
.student-form-wrapper {
  border: solid 0.4px #82cec4;
}
.student-form-wrapper .title,
.student-form-wrapper .prev-btn {
  color: #82cec4;
}
.student-form-wrapper .next-btn,
.student-form-wrapper .next-btn:hover,
.student-form-wrapper .next-btn:focus {
  background: #82cec4;
  border-color: #82cec4;
}
.student-last-step {
  border: solid 0.4px #82cec4;
}
.student-last-step button,
.student-last-step button:hover,
.student-last-step button:focus {
  background: #82cec4;
}
.student-last-step .title {
  color: #82cec4;
}
.header {
  padding: 40px 0;
}
.header .title {
  color: #39404c;
  font-weight: bold;
  font-size: 30px;
  margin-bottom: 0;
  font-family: 'DINNext-med';
  margin: 6px 0 0 7px;
}
.header span {
  font-size: 28px;
}
@media (max-width: 786px) {
  .header {
    margin: 0 20px;
  }
  .header .title {
    font-size: 20px;
  }
  .header img {
    width: 55px;
  }
}
.form-breif {
  height: 608px;
  padding: 27px 20px 40px 10px;
  border-radius: 10px;
  background: #e0efe6;
}
.form-breif .title {
  font-family: 'DINNext-reg';
  font-size: 28px;
  text-align: right;
  color: #39404c;
}
.form-breif .description {
  font-family: 'DINNext-reg';
  font-size: 18px;
  text-align: right;
  color: #39404c;
}
.form-breif img {
  border-radius: 50%;
  background: #84c774;
  padding: 8px;
  margin-top: 10px;
}
.student-form-breif {
  background: #e9f1f0;
}
.student-form-breif img {
  background: #82cec4;
}
.container-layout {
  min-height: 200px;
}
@font-face {
  font-family: 'DINNext-reg';
  src: url('./assets/fonts/din-next-lt-w23-regular.ttf');
  font-weigth: normal;
}
@font-face {
  font-family: 'DINNext-med';
  src: url('./assets/fonts/din-next-lt-w23-medium.ttf');
  font-weigth: 400;
}
body {
  background: #f7f7f7 !important;
}
